<template>
  <div class="">
    <div class="field">
      <canvas id="canvas"></canvas>
      <div v-for="y in size" :key="y" class="sell_vertical">
        <div :id="'sel' + y + '-' + x" v-for="x in size" :key="x" class="sell" @click="drawCrossNull(y, x)">&nbsp;
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import line from './line.js'
export const size = 15;

export default {
  name: 'FieldCrossNull',
  /*
    props: {
      size: {
        type: Number
      }
    },
  */
  data: () => ({
    visualCross: false,
    isToggleSign: false,
    size: size,
    mapSign: []
  }),
  mounted() {
    for (let i = 1; i < size + 1; i++) {
      this.mapSign[i] = [];
      for (let j = 1; j < size + 1; j++) {
        this.mapSign[i][j] = 0;
      }
    }
  },
  methods: {
    drawCrossNull(y, x, act) {
      const id = `sel${y}-${x}`;
      this.visualCross = true;
      this.isToggleSign = !this.isToggleSign;
      let symbol;
      let testSell;
      if (this.isToggleSign) {
        testSell = 'X';
        symbol = 1;
      } else {
        testSell = '0';
        symbol = 2;
      }
      document.getElementById(id).innerHTML = testSell;
      this.mapSign[y][x] = symbol;
      this.compareLineX(y, act);
      this.compareLineY(x, act);
      this.compareLineXY(y, x, act);
      this.compareLineYX(y, x, act);
    },

    // line(x, y, x1, y1) {
    //   let line = document.getElementById("canvas"),
    //     ctx = line.getContext('2d');
    //   line.height = size * 20 + 20;
    //   line.width = size * 20 + 20;
    //   ctx.beginPath();
    //   ctx.moveTo(x, y);
    //   ctx.lineTo(x1, y1);
    //   ctx.stroke();
    //   line.style.zIndex = 0;
    // },

    compareLineXY(y, x) {
      // Diagonal XY - \
      let x0 = x;
      let y0 = y;
      while (x !== 1 && y !== 1) {
        x0 = x0 - 1;
        y0 = y0 - 1;
        if (x0 === 1 || y0 === 1 || x0 === size || y0 === size) {
          break;
        }
      }
      let currentX = x0;
      let currentY = y0;
      let lastX = 0;
      let lastY = 0;
      let overlap = 1;
      const numSymbol = size < 5 ? 3 : 5;
      const delta = size < 5 ? 2 : 4;
      while (currentX < size) {
        if (currentX >= size || currentY >= size || currentX < 1 || currentY < 1) {
          break;
        }
        if (this.mapSign[currentY][currentX] === this.mapSign[currentY + 1][currentX + 1] && this.mapSign[currentY][currentX] !== 0) {
          overlap++;
          lastX = currentX + 1;
          lastY = currentY + 1;
        } else {
          overlap = 1;
        }
        if (overlap === numSymbol) {
          const startSellX = lastX - delta;
          const startSellY = lastY - delta;
          const idStart = `sel${startSellY}-${startSellX}`;
          const idEnd = `sel${lastY}-${lastX}`;
          const endX = document.getElementById(idEnd).offsetLeft + 15;
          const startX = document.getElementById(idStart).offsetLeft + 5;
          const endY = document.getElementById(idEnd).offsetTop + 15;
          const startY = document.getElementById(idStart).offsetTop + 5;
          this.line(startX, startY, endX, endY);
        }
        currentX++;
        currentY++;
      }
    },

    compareLineYX(y, x) {
      // Diagonal YX - /
      let x0 = x;
      let y0 = y;
      while (x !== size && y !== 1) {
        x0 = x0 + 1;
        y0 = y0 - 1;
        if (x0 === 1 || y0 === 1 || x0 === size || y0 === size) {
          break;
        }
      }
      let currentX = x0;
      let currentY = y0;
      let lastX = 0;
      let lastY = 0;
      let overlap = 1;
      const numSymbol = size < 5 ? 3 : 5;
      const delta = size < 5 ? 2 : 4;
      while (currentX <= size) {
        if (currentX > size || currentY >= size || currentX < 1 || currentY < 1) break;
        if (this.mapSign[currentY][currentX] === this.mapSign[currentY + 1][currentX - 1] && this.mapSign[currentY][currentX] !== 0) {
          overlap++;
          lastX = currentX - 1;
          lastY = currentY + 1;
        } else {
          overlap = 1;
        }
        if (overlap === numSymbol) {
          const startSellX = lastX + delta;
          const startSellY = lastY - delta;
          const idStart = `sel${startSellY}-${startSellX}`;
          const idEnd = `sel${lastY}-${lastX}`;
          const endX = document.getElementById(idEnd).offsetLeft + 5;
          const startX = document.getElementById(idStart).offsetLeft + 15;
          const endY = document.getElementById(idEnd).offsetTop + 15;
          const startY = document.getElementById(idStart).offsetTop + 5;
          this.line(startX, startY, endX, endY);
        }
        currentX--;
        currentY++;
      }
    },

    compareLineX(y) {
      // horizontal
      // debugger
      let currentX = 1;
      let lastX = 1;
      let overlap = 1;
      // const numSymbol = size < 5 ? 3 : 5;
      const numSymbol = 5;
      // const delta = size < 5 ? 2 : 4;
      while (currentX < size) {
        if (this.mapSign[y][currentX] === this.mapSign[y][currentX + 1] && this.mapSign[y][currentX] !== 0) {
          overlap++;
          lastX = currentX + 1;
        } else {
          overlap = 1;
        }
        if (overlap === numSymbol) {
          const startSell = lastX - numSymbol + 1;
          // const startSell = lastX - delta;
          const idStart = `sel${y}-${startSell}`;
          const idEnd = `sel${y}-${lastX}`;
          // document.getElementById(idStart).style.background = "red"
          const startX = document.getElementById(idStart).offsetLeft + 5;
          const endX = document.getElementById(idEnd).offsetLeft + 15;
          const constY = document.getElementById(idEnd).offsetTop + 10;
          // this.line(startX, constY, endX, constY);
          // debugger
          line(startX, constY, endX, constY);
        }
        currentX++
      }
    },

    compareLineY(x) {
      // Vertical
      let currentY = 1;
      let lastY = 0;
      let overlap = 1;
      const numSymbol = size < 5 ? 3 : 5;
      const delta = size < 5 ? 2 : 4;
      while (currentY < size) {
        if (this.mapSign[currentY][x] === this.mapSign[currentY + 1][x] && this.mapSign[currentY][x] !== 0) {
          overlap++;
          lastY = currentY + 1;
        } else {
          overlap = 1;
        }
        if (overlap === numSymbol) {
          const startSell = lastY - delta;
          const idStart = `sel${startSell}-${x}`;
          const idEnd = `sel${lastY}-${x}`;
          const endY = document.getElementById(idEnd).offsetTop + 15;
          const startY = document.getElementById(idStart).offsetTop + 5;
          const constX = document.getElementById(idEnd).offsetLeft + 10;
          this.line(constX, startY, constX, endY);
        }
        currentY++
      }
    }
  }
}
</script>

<style scoped>
.sell {
  display: inline-block;
  border: 1px solid blue;
  width: 20px;
  height: 19px;
  padding-top: 1px;
  cursor: pointer;
  text-align: center;
}

.field {
  position: relative;
}

#canvas {
  position: absolute;
  z-index: -1;
}
</style>
