import { size } from "./FieldCrossNull.vue";

export default function line(x, y, x1, y1) {
  console.log("size", size);
  let cross = document.getElementById("canvas"),
    ctx = cross.getContext("2d");
  cross.height = size * 20 + 20;
  cross.width = size * 20 + 20;
  ctx.beginPath();
  ctx.moveTo(x, y);
  ctx.lineTo(x1, y1);
  ctx.stroke();
  cross.style.zIndex = 0;
}
